import React from "react";
import { Button } from "react-bootstrap";

const Contato = () => {
  return (
    <div className="bg-dark text-light p-4 text-center">
      <h2 className="mb-4">Contato</h2>
      <h4 className="mb-3">Quer encomendar uma peça personalizada ou tirar dúvidas?</h4>
      <p className="mb-4">Clique no botão abaixo e fale com a gente direto pelo Instagram!</p>

      <Button
        variant="primary"
        size="lg"
        href="https://www.instagram.com/uplevel3d"
        target="_blank"
        rel="noopener noreferrer"
      >
        Chamar no Direct
      </Button>
    </div>
  );
};

export default Contato;
